.box-container {
  justify-content: space-between;
  height: 54px;
  --boxSize: 4px;
  --gutter: 8px;
  width: calc((var(--boxSize) + var(--gutter)) * 13);
}

.box {
  transform: scale(0.8);
  height: 100%;
  width: var(--boxSize);
  background: rgb(54, 69, 98);
  background: radial-gradient(
    circle,
    rgb(255, 255, 255) 0%,
    rgba(201, 104, 80, 1) 100%
    );
    border-radius: 18px;
  }
  
  .box.active {
    animation-duration: 1.2s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
}

.box1 {
  animation-name: quiet;
}

.box2 {
  animation-name: normal;
}

.box3 {
  animation-name: quiet;
}

.box4 {
  animation-name: loud;
}

.box5 {
  animation-name: quiet;
}

.box6 {
  animation-name: normal;
}

.box7 {
  animation-name: quiet;
}

@keyframes quiet {
  25% {
    transform: scale(0.6);
  }
  50% {
    transform: scale(0.4);
  }
  75% {
    transform: scale(0.8);
  }
}

@keyframes normal {
  25% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.4);
  }
  75% {
    transform: scale(0.6);
  }
}

@keyframes loud {
  25% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.4);
  }
  75% {
    transform: scale(1.2);
  }
}
