.albumImage-card{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.albumImage{
    width: 80%;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 1;
}

.albumImage img{
    border-radius: 30px;
    width: 100%;
    aspect-ratio: 1;
    /* margin-left: 25%; */
}

.albumImage-shadow{
    -webkit-filter: blur(10px);
    filter:blur(10px);
    width: 90%;
    position: absolute;
    z-index: -1;
    top: 20px;
}